export const codeSample1 = `function Component() {
    const [first, setFirst] = useState(false);
    return(
        <div>
            <Checkbox
                value={first}
                onChange={setFirst}
                label={"Is this a radio button?"}
            />
        </div>
    )
}`;

export const properties = [
    {
        name: "value",
        format: "boolean",
        required: true,
        description: "Currently selected value of the radio button",
    },
    {
        name: "onChange",
        format: "Function",
        required: false,
        description:
            "Callback function to call when the radio button is toggled",
    },
    {
        name: "label",
        format: "string",
        required: false,
        description: "Label to display associated with the input",
    },
    {
        name: "color",
        format: "ColorType",
        required: false,
        description: "Color to use for the icon",
    },
];
